:root {
  --ss-primary-color: #babae6;
  --ss-bg-color: #2b2739;
  --ss-font-color: #fff;
  --ss-font-placeholder-color: #fff;
}

.loader-text {
  padding-left: 60px;
  font-size: 20px;
  font-weight: 700;
}

.error {
  color: #ff0202;
}

.cat-info {
  margin-top: 30px;
  display: flex;
}

.cat-info div {
  margin-left: 30px;
}

.is-hidden {
  display: none;
}

.loader {
  perspective: 1000px;
  width: 48px;
  height: 48px;
  color: #383838;
  border-radius: 50%;
  transform: rotateZ(45deg);
}

.loader:before, .loader:after {
  content: "";
  width: inherit;
  height: inherit;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  display: block;
  position: absolute;
  top: 45px;
  left: 10px;
  transform: rotateX(70deg);
}

.loader:after {
  color: #ff3d00;
  animation-delay: .4s;
  transform: rotateY(70deg);
}

@keyframes rotate {
  0% {
    transform: translate(-50%, -50%)rotateZ(0deg);
  }

  100% {
    transform: translate(-50%, -50%)rotateZ(360deg);
  }
}

@keyframes rotateccw {
  0% {
    transform: translate(-50%, -50%)rotate(0deg);
  }

  100% {
    transform: translate(-50%, -50%)rotate(-360deg);
  }
}

@keyframes spin {
  0%, 100% {
    box-shadow: .2em 0;
  }

  12% {
    box-shadow: .2em .2em;
  }

  25% {
    box-shadow: 0 .2em;
  }

  37% {
    box-shadow: -.2em .2em;
  }

  50% {
    box-shadow: -.2em 0;
  }

  62% {
    box-shadow: -.2em -.2em;
  }

  75% {
    box-shadow: 0 -.2em;
  }

  87% {
    box-shadow: .2em -.2em;
  }
}

